@media screen and (min-width: 480px) {
    .header {
        padding-top: 14px;
    }

    .header-div {
        width: 440px;
        height: 44px;
    }

    .header-logo-svg {
        width: 48px;
        height: 44px;
    }

    .header-modal-open {
        top: 0;
    }
}

@media screen and (min-width: 768px) {
    .header {
        padding-top: 20px;
    }

    .header-div {
        width: 704px;
        height: 46px;
    }

    .header-modal-open {
        top: 9px;
        left: 488px;
    }

    .header-button {
        display: flex;
        padding: 0;
        border: 0;
        align-items: center;
        justify-content: center;
        gap: 8px;
        position: absolute;
        right: 0;
        bottom: 0;
        width: 152px;
        height: 42px;
        background-color: var(--main-yellow);
        border-radius: 10px;
        transition: background-color 250ms var(--cubic-bezier);
    }
    .header-button:hover,
    .header-button:focus {
        background-color: var(--button-green);
        outline: none;
    }


    .header-button-span-text {
        width: 65px;
        height: 18px;
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 1.21;
        display: flex;
        align-items: center;
        text-align: center;
        color: #000000;
        transition: color 250ms var(--cubic-bezier);
    }

    .header-button-span-svg {
        transition: fill 250ms var(--cubic-bezier);    }


    .header-button:focus .header-button-span-text {
        color: var(--white);
    }

    .header-button:focus .header-button-span-svg {
        fill: var(--white);
    }

}

@media screen and (min-width: 1200px) {
    .header {
        padding-top: 8px;
    }

    .header-div {
        width: 1168px;
        height: 65px;
    }

    .logo:focus {
        outline: none;
    }

    .header-logo-svg {
        width: 60px;
        height: 65px;
    }

    .header-modal-open {
        display: none;
    }

    .header-button {
        top: 20px;
        left: 1016px;
        padding: 0;
        border: 0;
    }

    .site-nav {
        display: flex;
        justify-content: space-between;
        position: absolute;
        left: 368px;
        top: 32px;
        width: 444px;
        height: 20px;
    }

    .site-nav-item {
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 1.21;
        display: flex;
		align-items: center;
    }

    .site-nav-item .link {
        color: var(--text-light);
        transition: color 250ms var(--cubic-bezier);
    }
    .site-nav-item .link:hover,
    .site-nav-item .link:focus {
         color: var(--main-yellow);
         outline: none;
    }
}